import { Component, OnInit, Inject, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ErrorHandleService, BillSundryMasterService, CurrentUserService, AccountHeadService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxCheckBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcheckbox';

@Component({
  selector: 'app-bill-sundry-master-add',
  templateUrl: './bill-sundry-master-add.component.html',
  styleUrls: ['./bill-sundry-master-add.component.scss']
})
export class BillSundryMasterAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;

  @ViewChild('sale') sale: jqxCheckBoxComponent;
  @ViewChild('purchase') purchase: jqxCheckBoxComponent;

  @ViewChild('purchaseTypeCombo') purchaseTypeCombo:jqxComboBoxComponent;
  @ViewChild('fxValuationCombo') fxValuationCombo:jqxComboBoxComponent;
  @ViewChild('proportionCombo') proportionCombo:jqxComboBoxComponent;

  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  account_head: any;
  accountAdapter: any;
  apiUrl: string;

  saleValueCheck: boolean = false;
  purchaseValueCheck: boolean = false;


  bsmForm: FormGroup;
  userData: any = {};

  purchase_type:any = null;
  // purchase_dr_item_wise_type:any = null;
  // purchase_dr_whole_sum:number;
  // purchase_cr_type:any = null;

  purchaseTypeAdapter: any = [
    { text: 'Item wise', value: 'ITEMWISE' },
    { text: 'Wholesum', value: 'WHOLESUM' },
    // { text: 'Group wise', value: 'GROUPWISE' }
  ];

  fxValuationAdapter:any = [
    { text: 'No', value: 0 },
    { text: 'Yes', value: 1 }
  ];

  proportionAdapter:any = [
    { text: 'Acc. to Pricing', value: 'ACCPR' },
    { text: 'Quantity', value: 'QTY' },
    { text: 'Equal', value: 'EQL' }
  ];


  constructor(
    private fb: FormBuilder,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
    private bsms: BillSundryMasterService,
    private ahs: AccountHeadService,
    @Inject('API_URL') apiUrl: string, ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }
  ngOnInit() {
    // this.ahs.index({limit:'all', transact: 1}).subscribe(
    //   response => {
    //     this.accountAdapter = response;
    //   },
    //   error => {
    //     console.log(error);
    //   }
    // );
    // this.account_head =
    //   {
    //     datatype: "json",
    //     contentType: "application/json; charset=utf-8",
    //     datafields: [
    //       { name: 'id' },
    //       { name: 'name' },
    //     ],
    //     data: this.userData,
    //     async: false,
    //     url: this.apiUrl + 'account_head?limit=all',
    //   };
    // this.accountAdapter = new jqx.dataAdapter(this.account_head, {
    //   downloadComplete: (data, status, xhr) => {
    //     let tData = data.data;
    //     return tData;
    //   }
    // });

  }
  createForm() {
    this.bsmForm = this.fb.group({
      'code': [null, Validators.required],
      'name': [null, Validators.required],
    });
  }

  CheckedSale($event) {
    this.saleValueCheck = $event && $event.args && $event.args.checked || false;
    // this.purchaseValueCheck = false;
    // this.purchase.uncheck();
  }

  CheckedPurchase($event) {
    // this.saleValueCheck = false;
    this.purchaseValueCheck = $event && $event.args && $event.args.checked || false;
    // this.sale.uncheck();
  }

  purchaseTypeChange($event){
    this.purchase_type = this.purchaseTypeCombo.val() || false;
  }

  saveBtn(post) {
    let postData = this.buildPostObj(post);

    console.log(postData);
    this.myValidator.validate(document.getElementById('bsmForm'));

    if (postData['purchase'] || postData['sale']) {
      this.jqxLoader.open();
      this.bsms.store(postData).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }

  buildPostObj(postData){
    this.saleValueCheck == true ? postData['sale'] = 1 : postData['sale'] = 0 ;
    this.purchaseValueCheck == true ? postData['purchase'] = 1 : postData['purchase'] = 0;
    if(this.purchaseValueCheck == true){

      // get current purchase type 'ITEMWISE' or 'WHOLESUM'
      postData['purchase_type'] = this.purchase_type;
      postData['effect_valuation'] = this.fxValuationCombo.val() || 0;

      if(this.purchase_type == 'WHOLESUM' || this.purchase_type == 'GROUPWISE'){
        // if type == 'WHOLESUM'
        postData['proportion'] = this.proportionCombo.val();
      }else{
        postData['proportion'] = null;
      }

    }

    return postData;
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }

  rules =
    [
      { input: '.code', message: 'Code is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.name', message: 'Name is required!', action: 'keydown, blur', rule: 'required' },
      //   { input: '.nature', message: 'Nature is required!', action: 'keydown, blur', rule: 'required' },

      //   { input: '.type', message: 'Type is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
      //    let data = this.type.val();
      //    let result = (data!='')?true:false;
      //    return result;
      //  } },     
    ];

}


