import { BillSundryMasterAddComponent } from './bill-sundry-master-add/bill-sundry-master-add.component';
import { BillSundryMasterEditComponent } from './bill-sundry-master-edit/bill-sundry-master-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    BillSundryMasterRoutingModule
  ],
  declarations: [
    BillSundryMasterComponent,
    BillSundryMasterAddComponent,
    BillSundryMasterEditComponent
],
entryComponents:[
  BillSundryMasterAddComponent,
  BillSundryMasterEditComponent
]
})
export class BillSundryMasterModule { }



